<template>
  <div class="main-wrapper">
    <div class="question-page">
      <h2>Вопрос id: {{ questionId }}</h2>
      <!-- <div>
        <button>
          Перейти к списку тестов
        </button>
      </div> -->
      <div>
        <button
          v-if="parentId"
          @click="moveToParentId"
        >
          Перейти к родителю {{ parentId }}
        </button>
      </div>

      <div class="text-question-wrapper">
        Текст:
        <textarea
          v-model.lazy="infoText"
          class="text-info-question"
        >
        </textarea>        
      </div>

      <div class="text-question-wrapper">
        Вопрос:
        <textarea
          v-model.lazy="questionText"
          class="text-info-question"
        >          
        </textarea>
      </div>
      <hr style="width: 700px;">
      <div>
        <button
        @click="ADD_QUESTION"
        >Добавить новый узел</button>
      </div>

      <div class="branch-list">
        <div
          v-for="(item, index) in answerTransitions" 
          :key="index"
          class="branch-item"
        >
          <div class="column-id">
            id: {{ item.transitionId }}
          </div>
          <div>
            <div>Текст ответа</div>
            <textarea
              v-model="item.answerText"
              class="answer-text"
            ></textarea>
          </div>
          <button
          @click="moveToId(item.transitionId)"
          >Перейти</button>
          <button
            @click="CHECK_AND_DELETE(item.transitionId)"
            
          >Удалить</button>

        </div>
      </div>

      <hr style="width: 700px;">

      <button
          @click ="saveData"
          class="button-save"
        >Сохранить</button>
      

    </div>
  </div>

</template>

<script>
  import axios from 'axios'

  export default {
    data() {
      return {
        serverURL: "https://dev1.med-game.ru/api/v1",
        testId: 11,
        questionId: 563,
        parentId: 0,
        parentInfoText: "",
        parentQuestionText: "",
        infoText: "",
        questionText: "",
        answerTransitions: [],

        token: "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJleHAiOjE2ODExMDY1NDEuNDcwNzgzLCJpYXQiOjE2ODAwMjY1NDEuNDcwNzg3LCJ1c2VybmFtZSI6ImFkbWluIiwicm9sZXMiOlsiYWRtaW4tcGFydCIsIm1hbmFnZS11c2VycyIsIm1hbmFnZS1yb2xlcyJdfQ.qngZS7Xu8nEkB1gZCa_UZIbqyONUCFF09S6IF3wzxlo"
      }
    },
    computed: {
      
    },
    methods: {
      GET_QUESTION_DATA(){
        axios({
          method: 'get',
          url: `${this.serverURL}/admin/question/${this.questionId}`,
          headers: { Authorization: `Bearer ${this.token}` }
        })
        .then((questionData) => {
          this.setData(questionData.data.data)
          console.log(questionData);
        })
        .catch((error) => {
          console.log('Ошибка:');
          console.log(error)
          return error
        })
      },

      UPDATE_QUESTION_DATA(){
        axios({
          url: `${this.serverURL}/admin/question/${this.questionId}`,
          method: 'put',
          headers: { Authorization: `Bearer ${this.token}` },
          data: {
            "id": this.questionId,
            "question_type_id": 2,
            "is_active": true,
            "test_id": this.testId,
            "seq_no": 1,
            "body": {
              "parentId": this.parentId,
              "infoText": this.infoText,
              "questionText": this.questionText,
              "answerTransitions": this.answerTransitions
            }
          }
        })
        .catch(function (error) {
          console.log(error);
      })  
      },

      DELETE_QUESTION(questionId){
        axios({
          url: `${this.serverURL}/admin/question/${questionId}`,
          method: 'delete',
          headers: { Authorization: `Bearer ${this.token}` },          
        })
        .then(() => {
          const deleteIndex = this.answerTransitions.findIndex(answer => answer.transitionId === questionId)
          if (deleteIndex>=0) {
            this.answerTransitions.splice(deleteIndex, 1)
          }
          this.UPDATE_QUESTION_DATA()
        })
        .catch(function (error) {
          console.log(error);
      })  
      },

      // deleteFromList(transitionId){
      //   const deleteIndex = this.answerTransitions.findIndex(answer => answer.transitionId === transitionId)
      //     if (deleteIndex>=0) {
      //       this.answerTransitions.splice(deleteIndex, 1)
      //     }
      // },

      CHECK_AND_DELETE(questionId){
        axios({
          method: 'get',
          url: `${this.serverURL}/admin/question/${questionId}`,
          headers: { Authorization: `Bearer ${this.token}` }
        })
        .then((questionData) => {
          console.log(questionData.data.data.body.answerTransitions.length)
          if (questionData.data.data.body.answerTransitions.length) {
            console.log("У удаляемого объекта есть зависимые узлы: ", questionData.data.data.body.answerTransitions.length);
            console.log("Нельзя удалять объект");
          }
          else {
            console.log("Удаляем объект")
            this.DELETE_QUESTION(questionId)
          }          
        })
        // .then((canDelete) => {
        //   if (canDelete) {
        //     console.log("Удаляем объект")
        //   this.DELETE_QUESTION(questionId)
        //   }
        //   else{
        //     console.log("Не удаляем объект");
        //   }          
        // })
        .catch((error) => {
          console.log('Ошибка:');
          console.log(error)
          return error
        })
      },

      ADD_QUESTION(){
        axios({
          url: `${this.serverURL}/admin/questions`,
          method: 'post',
          headers: { Authorization: `Bearer ${this.token}` },
          data: {
            // "id": this.questionId,
            "question_type_id": 2,
            "is_active": true,
            "test_id": this.testId,
            "seq_no": 1,
            "body": {
              "parentId": this.questionId,
              "infoText": "",
              "questionText": "",
              "answerTransitions": []
            }
          }
        })
        .then((response) => {
          this.answerTransitions.push(
            {
              "transitionId": response.data.data.id,
              "answerText": ""
            }
          )
          this.UPDATE_QUESTION_DATA()
        })
        .catch(function (error) {
          console.log(error);
        })        
      },

      setData(data){
        this.parentId = data.body.parentId,
        this.infoText = data.body.infoText,
        this.questionText = data.body.questionText,
        this.answerTransitions = data.body.answerTransitions
      },

      saveData(){
        this.UPDATE_QUESTION_DATA()
      },

      moveToId(transitionId){
        this.questionId = transitionId
        this.GET_QUESTION_DATA()
      },
      
      moveToParentId(){
        if (this.parentId) {
          this.questionId = this.parentId
          this.GET_QUESTION_DATA()
        }
        else{
          console.log("Не задан parentId")
        }
        
      }

    },
    mounted() {
      this.GET_QUESTION_DATA()
    },
    // watch:{
    //   infoText(newInfoText){
    //     console.log(newInfoText);
    //   }
    // }
  }
</script>

<style>
  .main-wrapper{
    display: flex;
    justify-content: center;
  }
  .question-page{
    display: flex;
    flex-direction: column;
    width: 700px;
    margin-top: 50px;
  }
  .branch-list{
    display: flex;
    flex-direction: column;
  }
  .branch-item{
    display: flex;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .text-question-wrapper{
    display: flex;
    margin: 20px;
  }
  button{
    margin: 5px;
  }
  .text-info-question{
    width: 600px;
    height: 100px;
  }
  .column-id{
    display: flex;
    align-items: center;
    margin-right: 10px;
  }
  .answer-text{
    width: 400px;
    height: 100px;
  }
  .button-save{
    height: 40px;
  }
</style>